/* General container styling */
.grid-container-wrapper {
  width: 80vw;
  height: 60vh;
  margin: 20px auto;
  border: 1px solid #333;
  position: relative;
  overflow: hidden;
  background: #f0f0f0;
  overscroll-behavior: contain;
}

.grid-container {
  position: relative;
  background: #fff;
}

/* Grid cell styling */
.grid-cell {
  width: 100px;
  height: 100px;
  border: 1px solid #aaa;
  box-sizing: border-box;
  float: left;
  position: relative;
}

.grid-cell.selected {
  background-color: lightgreen;
}

/* Container for images inside a cell */
.cell-content {
  width: 100%;
  height: 100%;
}

/* Style for each dropped vegetable image */
.cell-content img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

/* Mode toggle styling */
.mode-toggle {
  text-align: center;
  margin-bottom: 10px;
}

/* Dropdown container styling */
.dropdown-container select {
  font-size: 14px;
}

/* Vegetable list styling */
.vegetable-list {
  position: absolute;
  right: 10px;
  top: 50px;
  width: 150px;
  background: #fff;
  border: 1px solid #ccc;
  z-index: 1000;
  padding: 5px;
}

.vegetable-item {
  display: flex;
  align-items: center;
  cursor: grab;
  margin-bottom: 5px;
}

.vegetable-item img {
  width: 50px;
  height: 50px;
  margin-right: 5px;
}
